<template>
  <div :class="$style.container">
    <img src="@/assets/hacker.png">
    <cds-texting format="h3">Tu saldo actual de ahorro es: {{ lastBalance | moneyFormatter(0, true) }} </cds-texting>
    <cds-texting format="h5">Jugando - da {{day}}</cds-texting>
    <vue-swing
      @throwout="onThrowout"
      @dragstart="asd"
      :config="config"
      ref="vueswing">
      <div
        v-for="(card, i) in cards"
        :key="i"
        :class="$style.card">
        <span>{{ card }}</span>
      </div>
    </vue-swing>
  </div>
</template>

<script>
import VueSwing from 'vue-swing';
import moment from 'moment';
import types from '@/store/types';
import { mapGetters, mapActions } from 'vuex';
import moneyFormatter from '@/utils/number';

export default {
  name: 'Home',
  components: {
    VueSwing,
  },
  data() {
    return {
      currentCard: 0,
      day: 0,
      config: {
        allowedDirections: [
          VueSwing.Direction.LEFT,
          VueSwing.Direction.RIGHT,
        ],
        minThrowOutDistance: 250,
        maxThrowOutDistance: 300,
      },
      cards: [],
    };
  },
  filters: {
    moneyFormatter,
  },
  computed: {
    ...mapGetters([
      'lastBalance',
    ]),
  },
  methods: {
    ...mapActions({
      initGame: types.actions.INIT_GAME,
      nextIteration: types.actions.NEXT_ITERATION,
    }),
    createCard() {
      let message;
      if (Math.random() < 0.6) {
        const randomFund = parseInt(((Math.random() * 10) % 10) / 2, 10);
        const fund = ['A', 'B', 'C', 'D', 'E'].slice(randomFund, randomFund + 1);
        message = `FyF Recomienda 100% al Fondo ${fund}`;
      } else {
        const p1 = parseInt((Math.random() * 100) / 2, 10);
        const p2 = 100 - p1;
        const randomFund1 = parseInt(((Math.random() * 10) % 10) / 2, 10);
        const randomFund2 = parseInt(((Math.random() * 10) % 10) / 2, 10);
        const fund1 = ['A', 'B', 'C', 'D', 'E'].slice(randomFund1, randomFund1 + 1);
        const fund2 = ['A', 'B', 'C', 'D', 'E'].slice(randomFund2, randomFund2 + 1);
        message = `FyF Recomienda ${p1}% al ${fund1} y ${p2} al ${fund2}%`;
      }
      this.cards.push(message);
    },
    onThrowout({ throwDirection }) {
      this.currentCard += 1;
      if (throwDirection === VueSwing.Direction.RIGHT) {
        // FixMe: Take recommendation
        this.nextIteration({ 
          recommendation: [{
              fund: 'A',
              porcentage: 0.75,
            },
            {
              fund: 'B',
              porcentage: 0.25,
            },
          ]
        });
      }
    },
    discard() {
      if (this.$refs.vueswing) {
        const { cards } = this.$refs.vueswing;
        if (this.currentCard < cards.length) {
          cards[this.currentCard].throwOut(-50, Math.random() * 100 - 50);
        }
      }
    },
  },
  created() {
    // FixMe: remove example
    const customRecommendedDistribution = [
      {
        fund: 'E',
        porcentage: 1.0,
      },
    ];
    this.initGame({
      initialDate: moment('01/01/2017'),
      initialBalance: 1000000,
      initialRecommendedDistribution: customRecommendedDistribution,
    });
    setInterval(
      () => {
        if (this.day % 4 === 0) {
          this.createCard();
        }
        this.day += 1;
        if (this.day % 4 === 0) {
          this.discard();
        }
        this.nextIteration({});
      },
      1000,
    );
  },
};
</script>
<style lang="scss" module>
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  & > * {
    margin: 1em 0;
  }
}
.card {
  align-items: center;
  background-color: #FFF2C1;
  border-radius: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  display: flex;
  font-size: 1.3em;
  height: 200px;
  justify-content: center;
  left: calc(50% - 100px);
  position: absolute;
  top: calc(70% - 100px);
  width: 200px;
}
</style>
