<template>
  <div :class="$style.container">
    <img
      :class="$style.image"
      src="@/assets/sit.png">
    <cds-texting format="h1">Qu edad tienes?</cds-texting>
    <cds-input v-model="value"/>
    <cds-button
      @click="nextStep"
      :text="'Continuar'"/>
  </div>
</template>

<script>
// @ is an alias to /src
import { mapActions } from 'vuex';
import types from '@/store/types';

export default {
  name: 'Age',
  data() {
    return {
      value: '30',
    };
  },
  methods: {
    nextStep() {
      this.save({ input: 'age', value: parseInt(this.value, 10) });
      this.$router.push({ path: '/genero' });
    },
    created() {
      this.getShareValues();
      this.getFyFSuggestions();
    },
    ...mapActions({
      save: types.actions.SAVE_INPUT,
    }),
  },
  created() {
    this.$featureToggle.merge({ ADDED_FEATURE: true });
  },
};
</script>

<style lang="scss" module>
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  & > * {
    margin: 1em 0;
  }
}
</style>
