import Vue from 'vue';
import Vuex from 'vuex';
import types from '@/store/types';
import async from './modules/async';
import moment from 'moment';

Vue.use(Vuex);

export const actions = {
  [types.actions.INIT_GAME]: ({ getters, commit }, { initialDate, initialBalance, initialRecommendedDistribution }) => {
    // 1 - GET values for today
    const funds = getters.getFundsByDate[`${initialDate.format('MM/DD/YYYY')}`];

    const newDistribution = initialRecommendedDistribution.map(recom => ({
      fund: recom.fund,
      share: (initialBalance * recom.porcentage) / funds[recom.fund],
    }));

    commit(types.mutations.SET_CURRENT_DATE, initialDate);
    commit(types.mutations.SET_CURRENT_BALANCE, initialBalance);
    commit(types.mutations.SET_CURRENT_DISTRIBUTION, newDistribution);
  },
  [types.actions.SAVE_INPUT]: ({ commit }, { input, value }) => {
    switch(input) {
      case 'gender':
        commit(types.mutations.SET_GENDER, value);
        break;
      case 'age':
        commit(types.mutations.SET_AGE, value);
        break;
    }
  },
  [types.actions.NEXT_ITERATION]: ({ state, getters, commit }, { recommendation }) => {
    // 1 - GET values for today
    const funds = getters.getFundsByDate[`${state.currentDate.format('MM/DD/YYYY')}`];

    // 2 - Calculate balance for today
    const currentBalance = state.currentDistribution.reduce((accumulator, currentValue) => (
      accumulator + (currentValue.share * funds[currentValue.fund])
    ), 0);

    if (!!recommendation) {
      // 3 - Distribute the balance in new shares
      const newDistribution = recommendation.map(recom => ({
        fund: recom.fund,
        share: (currentBalance * recom.porcentage) / funds[recom.fund],
      }));
      commit(types.mutations.SET_CURRENT_DISTRIBUTION, newDistribution);
    }

    // 4 - Save balance and new distribution
    commit(types.mutations.SET_NEXT_DATE);
    commit(types.mutations.SET_CURRENT_BALANCE, currentBalance);
  },
};

export const getters = {
  [types.getters.SUGGESTED_STARTER_FUND]: (state) => {
    const recomendation = [
      { upperLimit: 99, lowerLimit: 24, recommendedFund: 'A' },
      { upperLimit: 23, lowerLimit: 14, recommendedFund: 'B' },
      { upperLimit: 13, lowerLimit: 9, recommendedFund: 'C' },
      { upperLimit: 8, lowerLimit: 5, recommendedFund: 'D' },
      { upperLimit: 4, lowerLimit: 0, recommendedFund: 'E' },
    ];
    const retirementAge = state.gender === 'Male' ? 65 : 60;
    const horizon = retirementAge - state.age;
    return recomendation
      .find(({ upperLimit, lowerLimit }) => lowerLimit <= horizon && horizon <= upperLimit)
      .recommendedFund;
  },
  [types.getters.LAST_BALANCE]: (state) => {
    return state.balances[state.balances.length - 1];
  },
  [types.getters.GET_FUNDS_BY_DATE]: (state) => {
    return state.async.getShareValues.data;
  },
};

export const mutations = {
  [types.mutations.SET_GENDER](state, value) {
    Vue.set(state, 'gender', value);
  },
  [types.mutations.SET_AGE](state, value) {
    Vue.set(state, 'age', value);
  },
  [types.mutations.SET_CURRENT_DATE](state, value) {
    Vue.set(state, 'currentDate', value);
  },
  [types.mutations.SET_CURRENT_BALANCE](state, balance) {
    state.balances.push(balance);
  },
  [types.mutations.SET_CURRENT_DISTRIBUTION](state, value) {
    Vue.set(state, 'currentDistribution', value);
  },
  [types.mutations.SET_NEXT_DATE](state) {
    Vue.set(state, 'currentDate', moment(state.currentDate).add(1, 'days'));
  },
};

export default new Vuex.Store({
  modules: {
    async,
  },
  state: {
    gender: undefined,
    age: undefined,
    currentDate: null,
    currentDistribution: [],
    balances: [],
  },
  actions,
  getters,
  mutations,
  strict: process.env.NODE_ENV !== 'production',
});
