<template>
  <div :class="$style.container">
    <img
      alt="Vue logo"
      :class="$style.image"
      src="@/assets/bike.png">
    <cds-texting format="h1">La carrera de tu vida</cds-texting>
    <cds-button
      @click="startGame"
      :text="'Vamos'"/>
  </div>
</template>

<script>
// @ is an alias to /src
import { mapActions } from 'vuex';

const textGetShareValues = 'getShareValues';
const textGetFyFSuggestions = 'getFyFSuggestions';

export default {
  name: 'Home',
  methods: {
    startGame() {
      this.$router.push({ path: '/edad' });
    },
    ...mapActions({
      getShareValues: textGetShareValues,
      getFyFSuggestions: textGetFyFSuggestions,
    }),
  },
  created() {
    this.$featureToggle.merge({ ADDED_FEATURE: true });
    this.getShareValues();
    this.getFyFSuggestions();
  },
};
</script>

<style lang="scss" module>
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  & > * {
    margin: 1em 0;
  }
}
</style>
