const MODULE_NAME = 'featureToggle';
const SET_FEATURE = 'setFeatures';
const STATE_ENTRY = 'features';

const storeModuleFactory = vueInstance => ({
  state: {},
  mutations: {
    [SET_FEATURE]: (state, value) => {
      vueInstance.set(state, STATE_ENTRY, { ...state[STATE_ENTRY], ...value });
    },
  },
});

export default {
  install(Vue, options) {
    const { store } = options;
    const featuresStore = storeModuleFactory(Vue);
    store.registerModule(MODULE_NAME, featuresStore);
    store.commit(SET_FEATURE, options.features);
    Object.defineProperty(Vue.prototype, `$${MODULE_NAME}`, {
      get: () => ({
        features: Object.freeze(store.state[MODULE_NAME][STATE_ENTRY]),
        merge: (newFeatures) => { store.commit(SET_FEATURE, newFeatures); },
      }),
    });
  },
};
