import { createAsyncStore } from '@pfg/vue-async';
import { getShareValues, getFyFSuggestions } from '@/api/example';

const errorHandlers = {
  401: (error) => { console.log('401 error handler. error: ', error); },
};

const actionsData = [
  { actionType: 'getShareValues', asyncFn: getShareValues, errorHandlers },
  { actionType: 'getFyFSuggestions', asyncFn: getFyFSuggestions, errorHandlers },
];

export default {
  namespaced: false,
  ...createAsyncStore(actionsData),
};
