<script>
// @ is an alias to /src
import { mapActions } from 'vuex';
import types from '@/store/types';

export default {
  name: 'Gender',
  methods: {
    nextStep(gender) {
      this.save({ input: 'gender', value: gender });
      this.$router.push({ path: '/listo' });
    },
    created() {
      this.getShareValues();
      this.getFyFSuggestions();
    },
    ...mapActions({
      save: types.actions.SAVE_INPUT,
    }),
  },
  created() {
    this.$featureToggle.merge({ ADDED_FEATURE: true });
  },
};
</script>

