const repeat = (s, n) => new Array(n + 1).join(s);

export default (val, spaces = 1, showZero = false) => {
  let numValue = val;
  if (typeof val === 'string') {
    numValue = parseInt(val, 10);
  }
  if (numValue) {
    return `$${repeat(' ', spaces)}${Math.trunc(numValue).toString().replace(/(\d)(?=(\d{3})+\b)/g, '$1.')}`;
  }
  if (showZero) {
    return `$${repeat(' ', spaces)}0`;
  }
  return '$';
};
