import axios from 'axios';
import moment from 'moment';

const httpClientSuccess = axios.create({
  baseURL: 'https://www.cuprum.cl/ApiSitioPublico/SeriesValorCuota/ValorCuota', // valores cuota
  timeout: 60000,
  headers: {},
});

const httpClientUnauthorized = axios.create({
  baseURL: 'https://fa-hackaton.firebaseio.com/recomendacion.json', // recomendaciones
  timeout: 60000,
  headers: {},
});

export const getShareValues = async () => httpClientSuccess.get().then((data) => {
  const res = {};

  data.data.A.forEach((val, index) => {
    const [timestamp] = val;
    res[moment(timestamp).format('MM/DD/YYYY')] = {
      A: data.data.A[index][1],
      B: data.data.B[index][1],
      C: data.data.C[index][1],
      D: data.data.D[index][1],
      E: data.data.E[index][1],
    };
  });
  return res;
});
export const getFyFSuggestions = async () => httpClientUnauthorized.get().then((data) => {
  const res = []
  data.data.map(val => {
    res.push({ date: moment(val["Fecha inicio"]).format('MM/DD/YYYY'), suggestions: val["Sugerencia FyF"] });
  })
  return res;
});
